.button {
  cursor: pointer; }

@media (max-width: 767px) {
  .item-footer-trips__list:first-of-type {
    margin-right: 12px; } }

.item-footer-trips__list {
  font-size: 14px;
  line-height: 17px;
  text-align: left;
  margin-right: 40px; }
  @media (max-width: 1023px) {
    .item-footer-trips__list {
      margin-right: 47px; } }
  @media (max-width: 767px) {
    .item-footer-trips__list {
      text-align: center;
      margin-right: 0px; } }

@media (max-width: 767px) {
  .item-footer-trips__list:last-of-type {
    margin: 0 auto;
    grid-area: 2 / 1 / 3 / 3;
    margin-top: 10px; } }

.item-footer-trips__item {
  margin-bottom: 10px;
  white-space: nowrap; }

.item-footer-trips__item a {
  color: rgba(20, 21, 23, 0.5); }

.item-footer-trips__wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  text-align: center; }
  @media (max-width: 1440px) {
    .item-footer-trips__wrapper {
      justify-content: start; } }
  @media (max-width: 767px) {
    .item-footer-trips__wrapper {
      display: grid;
      grid-template-columns: 1fr 1fr;
      grid-template-rows: 1fr;
      margin-left: -20px;
      margin-right: -20px; } }

.main-nav {
  position: relative; }
  .main-nav__button {
    display: none; }
    @media (max-width: 1023px) {
      .main-nav__button {
        display: block;
        width: 72px;
        height: 50px;
        background-color: #fe7865;
        outline: none;
        border: none; } }
    @media (max-width: 767px) {
      .main-nav__button {
        width: 50px; } }
  @media (max-width: 1023px) {
    .main-nav__toggle {
      display: flex;
      width: 22px;
      height: 1px;
      background-color: #ffffff;
      position: absolute;
      top: 24px;
      left: 27px;
      right: 0;
      bottom: 0; } }
  @media (max-width: 1023px) and (max-width: 767px) {
    .main-nav__toggle {
      left: 14px; } }
  @media (max-width: 1023px) {
      .main-nav__toggle::before {
        position: absolute;
        content: '';
        top: -5px;
        left: 0;
        right: 0;
        bottom: 0;
        width: 22px;
        height: 1px;
        z-index: 10;
        background-color: #ffffff; }
      .main-nav__toggle::after {
        position: absolute;
        content: '';
        top: 5px;
        left: 0;
        right: 0;
        bottom: 0;
        width: 22px;
        height: 1px;
        z-index: 10;
        background-color: #ffffff; } }

.page-footer__list {
  /*     display: flex;
    flex: 1 1 33.3%;
    justify-content: space-between;
    align-items: start; */
  display: grid;
  grid-template-columns: 1fr 1fr 1fr; }
  @media (max-width: 1440px) {
    .page-footer__list {
      grid-template-columns: repeat(2, 1fr);
      grid-template-rows: repeat(2, 1fr); } }
  @media (max-width: 767px) {
    .page-footer__list {
      grid-template-columns: 1fr;
      grid-template-rows: auto; } }

